.App {
  text-align: center;
  pointer-events: none;
  -ms-touch-action: none;
      touch-action: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.App-fps {
  position: absolute;
  top: 1rem;
  right: 1rem;
  font-size: 8px;
  color: #fff;
}

.App-logo {
  /* animation: fadeIn .4s ease-in; */
  position: absolute;
  bottom: 0;
  right: 0;
  -webkit-transform: translate(-10%, -40%);
      -ms-transform: translate(-10%, -40%);
          transform: translate(-10%, -40%);
  max-width: 10rem;
  width: 100%;
  min-width: 8rem;
  pointer-events: all;
  -ms-touch-action: all;
      touch-action: all;
  -webkit-user-select: all;
     -moz-user-select: all;
      -ms-user-select: all;
          user-select: all;
}

.App-gradient {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  max-width: 600px;
  opacity: 0.5;
  mix-blend-mode: hue;
}

.App-icon-transparent,
.App-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 180px;
  pointer-events: none;
  -ms-touch-action: none;
      touch-action: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.App-icon-transparent {
  opacity: 0.06;
}

.App-icon-bg {
  position: absolute;
  top: 0;

  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  mix-blend-mode: multiply;
  pointer-events: none;
  -ms-touch-action: none;
      touch-action: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

canvas.blur {
  -webkit-filter: blur(20px) contrast(1.8) brightness(1.2) saturate(1.4);
          filter: blur(20px) contrast(1.8) brightness(1.2) saturate(1.4);
  /* filter: blur(30px) contrast(3); */
  /* filter: blur(10px) contrast(1.4) brightness(1.7) saturate(1.2); */
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}



